import * as React from "react";
import { render } from "react-dom";
import { HashRouter } from "react-router-dom";
import App from "./App";

setTimeout(function () {
  window.scrollTo(0, 10);
}, 1000);

const rootEl = document.getElementById("root");

render(
  <HashRouter basename={rootEl.dataset.baseUrl}>
    <App />
  </HashRouter>,
  rootEl,
);
